/* Saved.css */
.saved {
    text-align: center;
    margin: 20px;
  }
  
  .saved--recipes {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
  }
  
  .saved--recipe {
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
  
    &:hover {
      transform: scale(1.05);
    }
  
    img {
      max-width: 100%;
      height: auto;
      border-radius: 8px;
    }
  
    h3 {
      margin-top: 10px;
      font-size: 1.5rem;
      color: #333;
    }
  
    p {
      color: #666;
    }
  
    .recipe-details {
      margin-top: 20px;
  
      h4 {
        margin-bottom: 10px;
        font-size: 1.2rem;
        color: #444;
      }
  
      ul, ol {
        list-style-type: none;
        padding: 0;
      }
  
      li {
        margin-bottom: 5px;
        color: #777;
      }
    }
  }
  